
import { http } from "src/common/";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";

/**
 *
 * @param {*} type
 * @param {*} idVariabel
 * @returns
 */
export const setFormPertanyaanAction = (formType, formValue) => {
  return { type: "ADD_PERTANYAAN", formType, formValue };
};


export const GetPertanyaansAction = (type) => {
  return(dispatch) => {
      loadingStatus(true);
      http
        .get(
          `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterPertanyaan}/GetPertanyaans`
        )
        .then((res) => {
          loadingStatus(false);
          let data = res.data;
          if (data.isSuccess) {
            dispatch({ type, payload: data.data });
          } else {
            Swal.fire("", `${data.returnMessage}  `, "warning");
          }
        });
  }
}

export const AddPertanyaanAction = (data) => {
  return(dispatch) => {
    loadingStatus(true);
    let idUser = sessionStorage.getItem("idUser");
    let param = `?IdUser=${idUser}&Pertanyaan=${data.Pertanyaan}&BobotDefault=${data.BobotDefault}`;
    http.post(`${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterPertanyaan}/AddPertanyaan${param}`).then(res => {
      loadingStatus(true);
      let result = res.data;
      if (result.isSuccess) {
        Swal.fire("Berhasil", "Pertanyaan Berhasil ditambahkan", "success");

        dispatch({ type: "MODAL_PERTANYAAN", payload: false });
        dispatch(GetPertanyaansAction("GET_LIST_PERTANYAAN"));
      } else {
        Swal.fire("Gagal", `${result.returnMessage}`, "error");
      }

    })
  }
}
