import { http } from "src/common/";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";

export const setFormSettingsAction = (formType, formValue) => {
  return { type: "ADD_APPL_SETTINGS", formType, formValue };
};

export const GetApplSetingsAction = (type) => {
  return (dispatch) => {
    loadingStatus(true);
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/GetApplSettings`
      )
      .then((res) => {
        loadingStatus(false);
        let results = res.data;
        if (results.isSuccess) {
          dispatch({ type, payload: results.data });
        } else {
          Swal.fire("Gagal", `${results.returnMessage}`, "error");
        }
      });
  };
};

export const AddApplSettingAction = (data) => {
  return (dispatch) => {
    loadingStatus(true);
    const newData = { ...data };
    newData.valueType = 3;
    console.log(newData);
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/AddApplSetting`,
        newData
      )
      .then((res) => {

        let result = res.data;
        if (result.isSuccess) {
          Swal.fire("Berhasil", `Anda telah berhasil merubah data`, "success");
          dispatch(GetApplSetingsAction("GET_LIST_APPL_SETTINGS"));
          dispatch({ type: "RESET_FORM_SETTING" });
        } else {
          Swal.fire("Gagal", `${result.returnMessage}`, "error");
        }
        loadingStatus(false);
      });
  };
};

export const UpdateApplSettingAction = (data) => {
  return (dispatch) => {
    loadingStatus(true);
    const newData = { ...data };
    newData.valueType = 3;
    newData.valueDate = 'string';
    newData.valueNumber = 0;
    console.log(newData);
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/EditApplSetting`,
        newData
      )
      .then((res) => {
        let result = res.data;
        if (result.isSuccess) {

          dispatch(GetApplSetingsAction("GET_LIST_APPL_SETTINGS"));
          dispatch({ type: "RESET_FORM_SETTING" });
          Swal.fire("Berhasil", `${result.returnMessage}`, "success");
        } else {
          Swal.fire("Gagal", `${result.returnMessage}`, "error");
        }
        loadingStatus(false)
      });
  };
};

export const GetApplSetingByCodeAction = (code) => {
  return(dispatch) => {
    http.get(`${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/GetApplSetting?Code=${code}`).then(res => {
      let result = res.data;
      if(result.isSuccess) {
        dispatch(setFormSettingsAction("code",result.data.code))
        dispatch(setFormSettingsAction("name",result.data.name))
        dispatch(setFormSettingsAction("valueString",result.data.valueString))
      }else{
        dispatch({ type: "RESET_FORM_SETTING" });
        Swal.fire("Error",`${result.returnMessage}`,"error");
      }
    })
  }
}

export const DeleteApplSettingAction = (code) => {
  return (dispatch) => {
    Swal.fire({
      title: "Apakah anda yakin?",
      text: `Akan menghapus ${code}`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Ya, Akan Aktifkan",
    }).then((result) => {
      if (result.isConfirmed) {
        http
          .post(
            `${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/DeleteApplSetting?Code=${code}`
          )
          .then((res) => {
            let result = res.data;
            if (result.isSuccess) {
              Swal.fire(`Sukses`, `Data ${code} berhasil dihapus`, `success`);

              dispatch(GetApplSetingsAction("GET_LIST_APPL_SETTINGS"));
            } else {
              Swal.fire("Gagal", `${result.returnMessage}`, "error");
            }
          });
      }
    });
  };
}
