import { http } from "src/common/";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";


export const setFormFilterMonev = (formType, formValue) => {
  return { type: "FILTER_PERTANYAAN", formType, formValue };
};

export const GetMonevByYearAction = (type, year) => {
  return (dispatch) => {
    let idUser = sessionStorage.getItem("idUser");
    loadingStatus(true);
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMonev}/GetMonevs?IdUser=${idUser}&Tahun=${year}`
      )
      .then((res) => {
        loadingStatus(false)
        let result = res.data;
        if (result.isSuccess) {
          dispatch({ type, payload: result.data });
        } else {
          Swal.fire("", `${result.returnMessage}  `, "error");
           dispatch({ type, payload: result.data });
        }
      });
  };
};

export const GetDetailMOnevByIdMonevAction = (id) => {
  return (dispatch) => {
    http.get(`${process.env.REACT_APP_ROOT_API}${serviceName.svcMonev}/GetMonev?IdMonev=${id}`).then(res => {
      let results = res.data;
      if(results.isSuccess){
        dispatch({ type: "GET_LIST_MONEV_BY_IDMONEV", payload: results.data });
      }else{
        Swal.fire("Gagal",`${results.returnMessage}`,"error");
        dispatch({ type: "GET_LIST_MONEV_BY_IDMONEV", payload: results.data });
      }
    })
  }
}

export const DeleteMonevAction = (IdMonev, year) => {
  let idUser = sessionStorage.getItem("idUser");
  return (dispatch) => {
    Swal.fire({
      title: "Apakah anda yakin?",
      text: `Akan menghapus data monev ${IdMonev}`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Ya, Akan Menghapus",
    }).then((result) => {
      if (result.isConfirmed) {
        http
          .post(
            `${process.env.REACT_APP_ROOT_API}${serviceName.svcMonev}/DeleteMonev?IdUser=${idUser}&IdMonev=${IdMonev}`
          )
          .then((res) => {
            let result = res.data;
            if (result.isSuccess) {
              Swal.fire(`Sukses`, `Data Id ${IdMonev} Monev berhasil dihapus`, `success`);
              dispatch(GetMonevByYearAction("GET_LIST_MONEVS", year));
            } else {
              Swal.fire("Gagal", `${result.returnMessage}`, "error");
            }
          });
      }
    });
  };
};

